<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>轮播图添加</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form-box">
                <el-form ref="form" :model="parames" label-width="100px">
                    
                    <el-form-item label="轮播图">
                        <images-upload :tips="tips" :moreUpload="false" :index="1" :onSuccess="uploadSuccess"></images-upload>
                    </el-form-item>
                    
                    <el-form-item label="商品goodsId">
                        <el-input placeholder="请输入goodsId" v-model="parames.actionUrl" clearable></el-input>
                    </el-form-item>
                    

                    <el-form-item>
                        <el-button @click="onSubmit(false)">返回</el-button>
                        <el-button type="primary" @click="onSubmit(true,1)">确定</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "bannerAdd",
    data() {
        return {
            tips:'只支持png jpeg jpg 格式文件，内存不超过1MB，图片建议尺寸750*400',
            parames: {
                actionUrl: null,
                bannerImg: null,
            },
        };
    },

    methods: {

        //添加轮播图
        async bannerAdd(categoryName) {
            const data = await this.$api.BannerAdd(this.parames);
            //返回列表刷新页面
            this.$router.push({
                name: "bannerList",
                params: {
                    onload: true,
                    onloadDeep: true
                }
            });
        },

        //图片添加成功
        uploadSuccess(imgUrl, index) {
            
            this.parames.bannerImg = imgUrl;
            
        },

        onSubmit(type, status) {
            if (!type) {
                this.$router.go(-1);
                return;
            }
            if (this.parames.actionUrl==null||this.parames.bannerImg==null) {
                this.$message.error("商品ID或图片不能为空");
                return;
            }
            this.bannerAdd();
        }
    }
};
</script>

